@use "sass:map"
@use "abstracts/functions/theme-get" as *

// $map: map of variables
// $selector: ':root' or '[data-theme="dark"]' etc.
@mixin define-css-vars($map, $selector: null, $keys: null)
  #{if($selector, $selector, ":root")}
    @if $keys != null
      @each $key in $keys
        --#{$key}: #{theme-get($map, $key)}
    @else
      @each $key, $value in $map
        --#{$key}: #{$value}
