@use "abstracts/modules/bg-cover" as *
@use "abstracts/mixins/overlay" as *
@use "abstracts/animations/fade-in-up" as *
@use "abstracts/animations/bounce" as *

.hero
  @extend %bg-cover
  position: relative
  width: 100%
  height: 100vh
  min-height: 100dvh
  background-image: url("../assets/img/hero-bg.png")
  display: flex
  align-items: center
  justify-content: center
  text-align: center
  color: var(--color-fg-on-emphasis)
  overflow-y: auto
  flex-direction: column
  padding: 1rem

  @include overlay(rgba(13, 17, 23, 0.6))

  @media (max-width: 768px)
    background-attachment: scroll

.hero-content
  position: relative
  z-index: 2
  padding: clamp(1rem, 4vw, 2rem)
  width: 100%
  max-width: none
  background: var(--color-bg-hero-content)
  border: 1px solid var(--color-border-hero-content)
  box-shadow: var(--shadow-hero-content-default)
  border-radius: 12px
  max-inline-size: 45rem
  margin-inline: auto
  backdrop-filter: blur(8px) saturate(140%) contrast(120%)
  animation: fadeInUp 0.8s var(--motion-easing-in) both

  @media (max-width: 768px)
    max-inline-size: 35rem

.hero-title
  font-size: clamp(1.75rem, 5vw, 2.5rem)
  line-height: 1.2
  margin-bottom: 0.5rem
  font-weight: 700
  border-bottom: none

.hero-subtitle
  font-size: clamp(0.9375rem, 1.6vw, 1.25rem)
  font-weight: 300
  opacity: 0.9

.hero-highlight
  letter-spacing: 1px
  border-bottom: 2px solid var(--color-border-accent-emphasis)

.hero-links
  margin-top: 1.5rem
  display: flex
  flex-wrap: wrap
  justify-content: center
  gap: 0.5rem

  @media (min-width: 768px)
    gap: 0.75rem

  @media (min-width: 1280px)
    gap: 1rem

.hero-link
  padding: 0.5rem 1rem
  font-size: clamp(0.875rem, 1.5vw, 1rem)
  color: var(--color-fg-on-emphasis)
  border: 2px solid var(--color-border-emphasis)
  background-color: var(--color-bg-transparent)
  text-decoration: none
  border-radius: 6px
  display: inline-flex
  align-items: center
  gap: 0.25rem
  transition: all var(--motion-duration-medium) ease

  svg
    transition: all var(--motion-duration-medium) ease

  &:hover,
  &:focus-visible
    background-color: hsla(0, 0%, 100%, .1)
    color: var(--color-fg-white)
    text-decoration: none

  @media (min-width: 1280px)
    padding: 0.75rem 1.5rem

  @media (max-width: 480px)
    width: 100%
    text-align: center

.scroll-indicator
  position: absolute
  bottom: 0
  left: 50%
  transform: translateX(-50%)
  font-size: 1.5rem
  opacity: 0.8
  color: var(--color-fg-white)

  .bounce
    animation: bounce 1.5s infinite
